import "./ClicksModal.css";
import React from "react";
import axios from "axios";
import { Modal, DatePicker } from "antd";

const { RangePicker } = DatePicker;

const ClicksModal = ({
  isModalVisible,
  setIsModalVisible,
  modalInfo,
  modalDate,
  setModalDate,
  setError,
}) => {
  React.useEffect(() => {
    if (modalInfo) {
      axios
        .get(`api/url/short/clickDates/${modalInfo.short}`)
        .then((res) => {
          console.log(res);
          const temp = {};
          res.shortUrl.clicksDates.forEach((date) => {
            date = new Date(date);
            const dateinformat =
              ("00" + (date.getMonth() + 1)).slice(-2) +
              "/" +
              ("00" + date.getDate()).slice(-2) +
              "/" +
              date.getFullYear();
            if (temp[dateinformat]) {
              temp[dateinformat] = temp[dateinformat] + 1;
            } else {
              temp[dateinformat] = 1;
            }
          });
          console.log("ahga");
          console.log(temp);
        })
        .catch((err) => {
          setError("Something went wrong :(");
          console.log(err);
        });
    }
  }, [modalInfo, setError]);
  return (
    <Modal
      title="Basic Modal"
      visible={isModalVisible}
      footer={null}
      closeIcon={<h2 onClick={() => setIsModalVisible(false)}>&#10006;</h2>}
    >
      <div className="modal-top-half">
        <div className="links">
          <p>
            Short Link :{" "}
            {
              <a
                target="_blank"
                rel="noreferrer"
                href={window.location.href + modalInfo?.short}
              >
                {modalInfo?.short}
              </a>
            }
          </p>
          <p>
            Long Link :{" "}
            {
              <a target="_blank" rel="noreferrer" href={modalInfo?.full}>
                {modalInfo?.full.slice(0, 50) + "..."}
              </a>
            }
          </p>
        </div>
        <RangePicker
          onChange={(d) => setModalDate(d)}
          disabledDate={(d) => d > Date.now()}
          value={modalDate}
        />
      </div>
      <p>Some contents...</p>
      <p>Some contents...</p>
      <p>Some contents...</p>
      <p>Some contents...</p>
    </Modal>
  );
};

export default ClicksModal;
