import "./App.css";
import React, { useState, useEffect } from "react";
import axios from "axios";
import { message, Menu } from "antd";
import { PublicClientApplication } from "@azure/msal-browser";
import Header from "./Header";
import Main from "./Main";
import List from "./List";
import config from "./config";

const displayError = (err) => {
  message.error(err);
};

const App = () => {
  let loginTimeoutID = null;
  const [current, SetCurrent] = useState("home");
  const [authenticated, setAuthenticated] = useState(false);
  const [mobileMode, setMobileMode] = useState(false);
  const [user, setUser] = useState(null);
  const [accessToken, setAccessToken] = useState(null);
  const [username, setUsername] = useState(null);
  const [authenticationButtonLoading, setAuthenticationButtonLoading] =
    useState(false);
  const [error, setError] = useState(null);
  const [shortURL, setShortURL] = useState(null);

  useEffect(() => {
    if (window.innerWidth < 768) {
      setMobileMode(true);
    } else {
      setMobileMode(false);
    }
    const windowResizeEventListener = window.addEventListener("resize", () => {
      if (window.innerWidth < 768) {
        setMobileMode(true);
      } else {
        setMobileMode(false);
      }
    });
    const accessToken = sessionStorage.getItem("accesstoken");
    const username = sessionStorage.getItem("username");
    const name = sessionStorage.getItem("name");
    if (accessToken && username && name) {
      setAuthenticationButtonLoading(true);
      axios
        .post("/api/authenticate", { accessToken, username })
        .then((res) => {
          setAuthenticationButtonLoading(false);
          if (res.status === 200) {
            setAuthenticated(true);
            setUser(name);
            setAccessToken(accessToken);
            setUsername(username);
          } else {
            setAuthenticationButtonLoading(false);
            setError("Something went wrong! :(");
          }
        })
        .catch((err) => {
          setAuthenticationButtonLoading(false);
          console.log(err);
          setError("Something went wrong! :(");
        });
    }
    return () => {
      window.removeEventListener("resize", windowResizeEventListener);
    };
  }, []);

  useEffect(() => {
    if (error) {
      displayError(error);
      setError(null);
    }
  }, [error]);

  const publicClientApplication = new PublicClientApplication({
    auth: {
      clientId: config.appId,
      redirectUri: config.redirectUri,
      authority: config.authority,
    },
  });

  const login = () => {
    setAuthenticationButtonLoading(true);
    try {
      publicClientApplication
        .loginPopup({
          scopes: config.scopes,
          prompt: "select_account",
        })
        .then((res) => {
          loginTimeoutID = setTimeout(logout, 3599998);
          setAuthenticationButtonLoading(false);
          setUser(res.account.name);
          setAccessToken(res.accessToken);
          sessionStorage.setItem("name", res.account.name);
          sessionStorage.setItem("username", res.account.username);
          sessionStorage.setItem("accesstoken", res.accessToken);
          setUsername(res.account.username);
          setAuthenticated(true);
        });
    } catch (err) {
      console.log(err);
      setError("Something went wrong! :(");
    }
  };

  const logout = () => {
    setAuthenticationButtonLoading(true);
    try {
      publicClientApplication.logoutPopup().then(() => {
        clearTimeout(loginTimeoutID);
        setAuthenticationButtonLoading(false);
        setUser(null);
        setAuthenticated(false);
        setAccessToken(null);
        setUsername(null);
        sessionStorage.removeItem("name");
        sessionStorage.removeItem("accesstoken");
        sessionStorage.removeItem("username");
      });
    } catch (err) {
      console.log(err);
      setError("Something went wrong! :(");
    }
  };

  const renderTab = () => {
    if (current === "home") {
      return (
        <Main
          authenticated={authenticated}
          accessToken={accessToken}
          username={username}
          setError={setError}
          mobileMode={mobileMode}
          shortURL={shortURL}
          setShortURL={setShortURL}
        />
      );
    } else if (current === "about") {
      return (
        <div className="app-about">
          GLAM URL shortener is a minimalistic link shortening platform built
          specifically for Oxford GLAM by
          <a
            target="_blank"
            rel="noreferrer"
            href="https://www.cloudmantra.net"
          >
            {` cloudmantra.net`}
            <sup>TM</sup>
          </a>
        </div>
      );
    } else if (current === "links" && authenticated) {
      return (
        <List
          accessToken={accessToken}
          username={username}
          setError={setError}
        />
      );
    } else {
      return null;
    }
  };

  return (
    <div>
      <div>
        <Header
          user={user}
          authenticated={authenticated}
          login={login}
          logout={logout}
          authenticationButtonLoading={authenticationButtonLoading}
          setAuthenticationButtonLoading={setAuthenticationButtonLoading}
        />
        <div className="menu-container">
          <Menu
            onClick={(e) => SetCurrent(e.key)}
            selectedKeys={[current]}
            mode="horizontal"
            style={{ justifyContent: "center" }}
          >
            <Menu.Item key="home">Home</Menu.Item>
            <Menu.Item key="about">About</Menu.Item>
            {authenticated ? <Menu.Item key="links">Links</Menu.Item> : null}
          </Menu>
        </div>
        {renderTab()}
      </div>
    </div>
  );
};

export default App;
