import "./List.css";
import React, { useState, useEffect } from "react";
import axios from "axios";
import {
  Table,
  Button,
  Popconfirm,
  Input,
  Select,
  Radio,
  Tooltip,
  message,
} from "antd";
import ClicksModal from "./ClicksModal";

const { Option } = Select;

const displaySuccess = (text) => {
  message.success(text);
};

const List = ({ username, accessToken, setError }) => {
  const [urls, setUrls] = useState([
    {
      short: "asdasdasdasd",
      full: "asdasdasdasda",
      clicks: 10,
    },
    {
      short: "boli",
      full: "asdasdasdasda",
      clicks: 10,
    },
  ]);
  const [count, setCount] = useState(0);
  const [selectedRows, setSelectedRows] = useState([]);
  const [loading, setLoading] = useState(false);
  const [loading1, setLoading1] = useState(false);
  const [loading2, setLoading2] = useState(false);
  const [loading3, setLoading3] = useState(false);
  const [disabledDelete, setDisabledDelete] = useState(false);
  const [disabledDisable, setDisabledDisable] = useState(false);
  const [disabledEnable, setDisabledEnable] = useState(false);
  const [search, setSearch] = useState("");
  const [searchDebounce, setSearchDebounce] = useState("");
  const [pageNumber, SetPageNumber] = useState(1);
  const [sort, setSort] = useState("latest");
  const [filter, setFilter] = useState(3);
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [modalInfo, setModalInfo] = useState(null);
  const [modalDate, setModalDate] = useState([]);

  const columns = [
    {
      title: "Created at",
      dataIndex: "createdAt",
      render: (d) => {
        const date = new Date(d);
        return (
          ("00" + (date.getMonth() + 1)).slice(-2) +
          "/" +
          ("00" + date.getDate()).slice(-2) +
          "/" +
          date.getFullYear() +
          " " +
          ("00" + date.getHours()).slice(-2) +
          ":" +
          ("00" + date.getMinutes()).slice(-2) +
          ":" +
          ("00" + date.getSeconds()).slice(-2)
        );
      },
    },
    {
      title: "Short URL",
      dataIndex: "short",
      render: (short, { idx }) => (
        <a
          target="_blank"
          rel="noreferrer"
          onClick={() => {
            const temp = [...urls];
            temp[idx] = {
              ...temp[idx],
              clicks: temp[idx].clicks + 1,
            };
            setUrls(temp);
          }}
          href={window.location.href + short}
        >
          {short}
        </a>
      ),
    },
    {
      title: "Long URL",
      dataIndex: "full",
      render: (full) => (
        <a target="_blank" rel="noreferrer" href={full}>
          {full.slice(0, 50) + "..."}
        </a>
      ),
    },
    {
      title: "Clicks",
      dataIndex: "clicks",
      render: (clicks, { short, full }) => (
        <p
          style={{ cursor: "pointer" }}
          onClick={() => {
            setModalInfo({ short, full });
            setModalDate([]);
            setIsModalVisible(true);
          }}
        >
          {clicks} &#x1F4C8;
        </p>
      ),
    },
    {
      title: "Protected",
      dataIndex: "password",
      render: (password) => (password ? <p>&#x1F512;</p> : <h3>-</h3>),
    },
    {
      title: "Status",
      dataIndex: "status",
      render: (status) => (status ? <p>Enabled</p> : <p>Disabled</p>),
    },
  ];

  useEffect(() => {
    axios
      .post("/api/url", {
        accessToken,
        username,
        pageNumber,
        search,
        sort,
        filter,
      })
      .then((res) => {
        let finalList = [];
        res.data.shortUrls.forEach(
          ({ short, full, clicks, _id, password, createdAt, status }, idx) => {
            finalList.push({
              short,
              full,
              clicks,
              key: _id,
              password: password ? true : false,
              createdAt,
              status,
              idx,
            });
          }
        );
        setUrls(finalList);
        axios
          .get(`/api/url/shortUrls/count/${username}`)
          .then(({ data: { count } }) => setCount(count))
          .catch((err) => {
            console.log(err);
            setError("Something went wrong");
          });
      })
      .catch((err) => {
        console.log(err);
        setError("Something went wrong");
      });
  }, [username, accessToken, setError, pageNumber, search, filter, sort]);

  useEffect(() => {
    const batched = () => {
      setSearch(searchDebounce);
      SetPageNumber(1);
    };
    const timerID = setTimeout(batched, 1000);
    return () => clearTimeout(timerID);
  }, [searchDebounce]);

  const deleteUrlsFromList = () => {
    setLoading(true);
    setDisabledDisable(true);
    setDisabledEnable(true);
    const ids = [];
    selectedRows.forEach((key) => {
      ids.push(key);
    });
    axios
      .delete("/api/url/shortUrls", { data: { accessToken, username, ids } })
      .then(() => {
        displaySuccess("Deleted");
        setLoading(false);
        setDisabledDisable(false);
        setDisabledEnable(false);
        SetPageNumber(1);
        setSelectedRows([]);
      })
      .catch((err) => {
        setSelectedRows([]);
        setLoading(false);
        setDisabledDisable(false);
        setDisabledEnable(false);
        console.log(err);
        setError("Something went wrong");
      });
  };

  const changeStatusUrlsFromList = (status) => {
    setDisabledDelete(true);
    if (status) {
      setDisabledDisable(true);
      setLoading3(true);
    } else {
      setDisabledEnable(true);
      setLoading2(true);
    }
    const ids = [];
    selectedRows.forEach((key) => {
      const url = urls.find((url) => url.key === key);
      if (!(url.status === status)) {
        ids.push(key);
      }
    });
    axios
      .patch("/api/url/shortUrls", { accessToken, username, ids, status })
      .then(() => {
        displaySuccess(status ? "Enabled" : "Disabled");
        setLoading2(false);
        setDisabledDelete(false);
        setSelectedRows([]);
        if (status) {
          setDisabledDisable(false);
          setLoading3(false);
        } else {
          setDisabledEnable(false);
          setLoading2(false);
        }
        let temp = [...urls];
        temp = temp.map((url) => {
          if (ids.some((key) => url.key === key)) {
            return { ...url, status };
          } else return url;
        });
        setUrls(temp);
      })
      .catch((err) => {
        setSelectedRows([]);
        setLoading2(false);
        setDisabledDelete(false);
        if (status) {
          setDisabledDisable(false);
          setLoading3(false);
        } else {
          setDisabledEnable(false);
          setLoading2(false);
        }
        console.log(err);
        setError("Something went wrong");
      });
  };

  const deleteAllUrls = () => {
    setLoading1(true);
    axios
      .delete("/api/url/shortUrls/all", { data: { accessToken, username } })
      .then(() => {
        setUrls([]);
        setCount(0);
        setLoading(false);
        SetPageNumber(1);
      })
      .catch((err) => {
        setLoading1(false);
        console.log(err);
        setError("Something went wrong");
      });
  };

  const rowSelection = {
    selectedRows,
    onChange: (selectedRowKeys) => setSelectedRows(selectedRowKeys),
  };
  const hasSelected = selectedRows.length > 0;

  return (
    <>
      <ClicksModal
        isModalVisible={isModalVisible}
        setIsModalVisible={setIsModalVisible}
        modalInfo={modalInfo}
        modalDate={modalDate}
        setModalDate={setModalDate}
        setError={setError}
      />
      <div className="list-container">
        <p>Table of links generated</p>
        <div>
          <div style={{ marginBottom: "1rem" }}>
            <Popconfirm
              title="Are you sure to delete selected URLs?"
              onCancel={deleteUrlsFromList}
              okText="Cancel"
              cancelText="Delete"
            >
              <Button
                style={{ marginRight: "0.5rem" }}
                type="danger"
                disabled={!hasSelected || disabledDelete}
                loading={loading}
              >
                Delete
              </Button>
            </Popconfirm>
            {`  `}
            <Popconfirm
              title="Are you sure to disable selected URLs?"
              onCancel={() => changeStatusUrlsFromList(false)}
              okText="Cancel"
              cancelText="Disable"
            >
              <Button
                style={{ marginLeft: "0.5rem", marginRight: "0.5rem" }}
                type="danger"
                disabled={!hasSelected || disabledDisable}
                loading={loading2}
              >
                Disable
              </Button>
            </Popconfirm>
            {`  `}
            <Popconfirm
              title="Are you sure to enable selected URLs?"
              onCancel={() => changeStatusUrlsFromList(true)}
              okText="Cancel"
              cancelText="Enable"
            >
              <Button
                style={{ marginLeft: "0.5rem" }}
                type="primary"
                disabled={!hasSelected || disabledEnable}
                loading={loading3}
              >
                Enable
              </Button>
            </Popconfirm>
            <span style={{ marginLeft: 8, position: "relative", top: "5px" }}>
              {hasSelected ? `Selected ${selectedRows.length} items` : ""}
            </span>
          </div>
          <div>
            <Input
              placeholder="Search...."
              value={searchDebounce}
              onChange={(e) => setSearchDebounce(e.target.value)}
            />
          </div>
          <div>
            <Tooltip title="Sort by">
              <Select
                value={sort}
                onChange={(v) => {
                  SetPageNumber(1);
                  setSort(v);
                }}
              >
                <Option value="latest">Latest</Option>
                <Option value="oldest">Oldest</Option>
                <Option value="mostclicked">Most clicked</Option>
                <Option value="leastclicked">Least clicked</Option>
              </Select>
            </Tooltip>
            <Tooltip title="Link enabled/disabled filter">
              <Radio.Group
                onChange={(e) => {
                  SetPageNumber(1);
                  setFilter(e.target.value);
                }}
                value={filter}
              >
                <Radio value={1}>Enabled</Radio>
                <Radio value={2}>Disabled</Radio>
                <Radio value={3}>Both</Radio>
              </Radio.Group>
            </Tooltip>
          </div>
        </div>
        <Table
          rowSelection={rowSelection}
          columns={columns}
          dataSource={urls}
          scroll={{ x: 1000 }}
          pagination={{
            current: pageNumber,
            total: count,
            showSizeChanger: false,
            onChange: (page) => SetPageNumber(page),
          }}
        />
        {count > 0 ? (
          <Popconfirm
            title="Are you sure to delete all URLs generated by you?"
            onCancel={deleteAllUrls}
            okText="Cancel"
            cancelText="Delete"
          >
            <Button
              style={{ margin: "1rem 0" }}
              type="danger"
              loading={loading1}
            >
              Delete all {count} links generated by you
            </Button>
          </Popconfirm>
        ) : null}
      </div>
    </>
  );
};

export default List;
